body{overflow-x:hidden}*{font-family:"Be Vietnam Pro",sans-serif;margin:0;padding:0}a{color:unset;text-decoration:none}.section__padding{padding:0 10rem}@media (max-width: 992px){.section__padding{padding:0 6rem}}@media (max-width: 768px){.section__padding{padding:0 4rem}}@media (max-width: 576px){.section__padding{padding:0 2rem}}.section__margin{margin:0 10rem}@media (max-width: 992px){.section__margin{margin:0 6rem}}@media (max-width: 768px){.section__margin{margin:0 4rem}}@media (max-width: 576px){.section__margin{margin:0 2rem}}.fade-in{-webkit-animation:fade-in 300ms ease-in-out;animation:fade-in 300ms ease-in-out}@-webkit-keyframes fade-in{from{opacity:0}to{opacity:1}}@keyframes fade-in{from{opacity:0}to{opacity:1}}

.navbar{display:flex;justify-content:space-between;align-items:center;padding-top:4rem;padding-bottom:4rem}@media (max-width: 768px){.navbar{padding-top:2.5rem;padding-bottom:2.5rem}}.navbar__links{display:flex;list-style:none}.navbar__links a{color:#242d52;font-size:13px;font-weight:500;transition:color 200ms ease-in}.navbar__links a:hover{color:#9095a7}.navbar__links a:not(:last-child){margin-right:2rem}@media (max-width: 992px){.navbar__links{display:none}}@media (max-width: 992px){.navbar__btn{display:none}}.navbar__menu-container{position:absolute;top:7rem;left:2rem;right:2rem;text-align:center;background:#fafafa;padding:2rem 3rem;z-index:2;border-radius:5px}.navbar__menu-links{font-size:18px;font-weight:500}.navbar__menu-links p{margin-bottom:2rem}@media (min-width: 993px){.navbar__menu{display:none}}

.btn{background:#f25f3a;color:#ffefeb;padding:1rem 2rem;font-size:14px;font-weight:500;border:none;border-radius:10rem;box-shadow:0px 4px 10px 2px rgba(243,97,60,0.3);cursor:pointer;transition:opacity 200ms ease-in}.btn:hover{opacity:0.7}

.feature{width:100%;display:flex;flex-direction:column;margin-bottom:3rem}@media (min-width: 993px){.feature{margin-bottom:2.5rem}}.feature__header{width:100%;display:flex;align-items:center;background:#ffefeb;border-radius:10rem;margin-bottom:1rem}@media (min-width: 993px){.feature__header{background:none}}.feature__number{padding:0.75rem 1.125rem;background:#f25f3a;color:#ffefeb;font-size:0.875rem;font-weight:500;border-radius:10rem}.feature__title{color:#242d52;font-size:1rem;font-weight:bold;padding-left:0.875rem}.feature__desc{color:#9095a7;font-size:0.875rem;line-height:200%;text-align:justify}@media (min-width: 993px){.feature__desc{margin-left:4rem}}

.header{display:flex;flex-direction:column;padding-bottom:6.25rem}@media (min-width: 993px){.header{flex-direction:row;justify-content:space-between;align-items:center;padding-bottom:8rem}}.header__bg{position:absolute;top:0;right:0;z-index:-1;width:85%}@media (min-width: 993px){.header__bg{width:45%}}.header__content{order:2;text-align:center}@media (min-width: 993px){.header__content{order:1;text-align:left;margin-right:5rem}}.header__content h1{font-size:2.5rem;color:#242d52;margin-bottom:1rem}@media (min-width: 1201px){.header__content h1{font-size:3.5rem;margin-bottom:2rem}}.header__content p{font-size:1rem;color:#9095a7;margin-bottom:2rem;line-height:200%}@media (min-width: 1201px){.header__content p{margin-bottom:2.5rem}}.header__image{width:100%;order:1}@media (min-width: 993px){.header__image{order:1}}.header__image img{width:100%}

.about{display:flex;flex-direction:column;justify-content:space-between;align-items:center;padding-bottom:6.25rem}@media (min-width: 993px){.about{flex-direction:row;padding-bottom:8rem}}.about__desc{text-align:center}@media (min-width: 993px){.about__desc{text-align:left;padding-right:5rem}}.about__desc h2{width:100%;margin-bottom:1.5rem;color:#242d52;font-size:1.75rem}@media (min-width: 993px){.about__desc h2{font-size:2.25rem}}.about__desc p{width:100%;margin:1.5rem 0 3.5rem 0;color:#9095a7;font-size:1rem;line-height:200%}

